import View from './View.js';
import icons from 'url:../../img/icons.svg';

class PaginationView extends View {
  _parentElement = document.querySelector('.pagination');

  addHandlerClick(handler) {
    this._parentElement.addEventListener(`click`, function (e) {
      const btn = e.target.closest(`.btn--inline`);

      if (!btn) return;

      const goToPage = +btn.dataset.goto;

      handler(goToPage);
    });
  }

  _generateMarkup() {
    const currentPage = this._data.page;
    const numPages = Math.ceil(
      this._data.result.length / this._data.resultsPerPage
    );

    // Page 1 and there ARE other pages
    if (currentPage === 1 && numPages > 1) {
      return this._generateMarkupButton(`next`, currentPage);
    }

    // LAST page
    if (currentPage === numPages && numPages > 1) {
      return this._generateMarkupButton(`prev`, currentPage);
    }

    // OTHER page
    if (currentPage < numPages) {
      return `
        ${this._generateMarkupButton(`prev`, currentPage)}
        ${this._generateMarkupButton(`next`, currentPage)}            
      `;
    }

    // Page 1 and there are NO other pages
    return ``;
  }

  _generateMarkupButton(btn = `prev`, currentPage) {
    return `
        <button data-goto="${
          btn === `prev` ? currentPage - 1 : currentPage + 1
        }" class="btn--inline pagination__btn--${btn}">
          <svg class="search__icon">
            <use href="${icons}${
      btn === `prev` ? `#icon-arrow-left"` : `#icon-arrow-right"`
    }></use>
          </svg>
          <span>Page ${
            btn === `prev` ? currentPage - 1 : currentPage + 1
          }</span>
        </button>      
      `;
  }
}

export default new PaginationView();
